@import url(https://fonts.googleapis.com/css2?family=Girassol&display=swap);
body{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    background-color: black;
}
.head{
  width: 100%;
  padding: 30px 0;
  text-align: center;
  font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
  text-transform: uppercase;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 600;
  color: black(39, 146, 85);
  background-color:darkcyan;
  font-size: 1.5rem;
  margin-bottom: 30px;
}
.foot{
  width: 100%;
  padding: 20px 0;
  text-align: center;
  font-family: 'Raleway', sans-serif;
  font-size: 24px;
  letter-spacing: 2px;
  font-weight: 700;
  color:black;
  background-color: darkcyan;
}
.container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  justify-content: center;
}
  .card {
    display:grid;
    grid-template-columns: 250px;
    grid-template-rows: 300px 80px 90px;
    grid-template-areas: "image" "text" "stats";
    font-family: 'Poppins', sans-serif;
    border-radius: 18px;
    background: white;
    box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.9);
    text-align: center;
    margin: 50px 150px;
    -webkit-transform: scale(.99);
            transform: scale(.99);
    position: relative;
  }
  .card:hover{
    -webkit-transform: scale(1.05,1.05);
            transform: scale(1.05,1.05);
  }
  .card img{
    width: 100%;
    height: 300px;
    border-radius: 20px;
    background-size: cover;
    object-fit: fill;
  }
  .card_info{
    position:relative;
    text-align: center;
    border-radius: 0px 0px 20px 20px;
    font-family: sans-serif;
    font-weight: bold;
    top: 15px;
    font-size: 15px;
    color: black;
    height: 40px; 
  }
  .card_category{
      font-size: 15px;
      font-family: 'Lora', serif;
      font-weight: 400;
  }
  .card_title{
      font-size: 20px;
      font-family: 'Lora', serif;
  }
  button{
    cursor: pointer;
    display:block;
    padding: 20px 45px;
    border-radius: 20px;
    line-height: 4px;
    text-decoration: none;
    text-align: center;
    border: 2px solid seagreen;
    color: blue;
    font-size: 16px;
    font-family: arial;
    position: relative;
    overflow: hidden;
    background: cornsilk;
    text-transform: uppercase;
    transition: all .35s;
    margin-top: 0px;
    margin-left: 25px;
  }
button:hover{
  background-color: black;
}
.foot{
  width: 100%;
  text-align: center;
  font-size: 13px;
  letter-spacing: 2px;
  font-weight: 600;
  color: rgb(39, 146, 85);
  background-color: #fff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

